      /* relevant styles */
.img__wrap {
    position: relative;
    height: 100%;
    width: 100%;
  }
  
  .img__description {
    position: static;
    top: 0;
    bottom: 0; 
    left: 0;
    right: 0;
    background: #11998ea2;
    font-family: 'Bebas Neue', cursive;

    color: white;
    visibility: hidden;
    opacity: 0;
    /* transition effect. not necessary */
    transition: opacity .2s, visibility .2s;
  }
  
  .img__wrap:hover .img__description {
    visibility: visible;
    
    height: 100%;
    opacity:1;
    transition: 1s ease;
  }
.spinner {
    margin: 100px auto 0;
    width: 70px;
    text-align: center;
  }
  
  .spinner > div {
    width: 18px;
    height: 18px;
    background-color: #11998e;
  
    border-radius: 100%;
    display: inline-block;
    animation: sk-bouncedelay 1.4s infinite ease-in-out both;
  }
  
  .spinner .bounce1 {
    animation-delay: -0.32s;
  }
  
  .spinner .bounce2 {
    animation-delay: -0.16s;
  }
  
  @keyframes sk-bouncedelay {
    0%, 80%, 100% {
      transform: scale(0);
    } 40% {
      transform: scale(1.0);
    }
  }

